import { Component, OnInit } from '@angular/core';
import { MenuService } from './app.menu.service';

@Component({
  selector: 'app-app-user',
  template: `
  <div class="menu-scroll-content">
      <ul class="navigation-menu">
        <li app-menuitem *ngFor="let item of model; let i = index;" [item]="item" [index]="i" [root]="true"></li>
      </ul>
  </div>
`,
  styles: [
  ]
})
export class AppUserComponent implements OnInit {
  public model: any[];

  constructor(private MenuService: MenuService) { }

  ngOnInit(): void {
    this.MenuService.getGetPendingCounts().toPromise().then(data => {
      this.modelBuilder({ pendingBusinessAccounts: data.data.pendingBusinessAccounts })
    })
  }

  modelBuilder({ pendingBusinessAccounts }) {
    return this.model = [
      { label: 'Basic Users', icon: 'tune', routerLink: ['/user/basic/list'] },
      { label: 'Business Users', icon: 'tune', badge: `${pendingBusinessAccounts}`, routerLink: ['/user/business/list'] },
      { label: 'Admins', icon: 'tune', routerLink: ['/user/admin/list'] },
      { label: 'Moderators', icon: 'tune', routerLink: ['/user/moderator/list'] },
      { label: 'Super Admins ', icon: 'tune', routerLink: ['/user/superadmin/list'] },
      // {
      //   label: 'User Management', icon: 'people',
      //   items: [
      //     { label: 'Basic Users', icon: 'tune', routerLink: ['/user/basic/list'] },
      //     { label: 'Business Users', icon: 'tune', badge: `${pendingBusinessAccounts}`, routerLink: ['/user/business/list'] },
      //     { label: 'Admins', icon: 'tune', routerLink: ['/user/admin/list'] },
      //     { label: 'Moderators', icon: 'tune', routerLink: ['/user/moderator/list'] },
      //     { label: 'Super Admins ', icon: 'tune', routerLink: ['/user/superadmin/list'] },
      //   ]
      // },
    ];
  }

}
